<template>
  <div class="page-one">
      <div class="page-one-img">
          <img :src="detail.img" alt="">
      </div>
      <div class="page-one-content">
          <h1>
          {{detail.author}}
        </h1>
        <p>
            {{detail.content}}
        </p>
      </div>
      <Page :obj="ppObj"></Page>
  </div>
</template>

<script>
import Page from '../components/pages'

import { getOne } from '../api.config'
export default {
    name:'one',
    components:{
        Page
    },
    data(){
        return {
            id: '',
            detail:{
                img:'',
                author:'',
                content:'',
                day:'',
                month:'',
                next:'',
                num:'',
                pre:'',
            }
        }
    },
    created(){
        let { id } = this.$route.params
        getOne(id).then(res=> {
            this.detail = res.data
        })
    },
    watch:{
        '$route' (to, from) {
            if (to.params.id!== from.params.id) {
                let { id } = this.$route.params
                getOne(id).then(res=> {
                    this.detail = res.data
                })
            }
        }
    },
    computed:{
        ppObj(){
            const { month,next,num,pre  } = this.detail
            const data = {month,next,num,pre,type:'one'}
            return data
        }
    }
}
</script>

<style lang="stylus">
.page-one
    position relative
    &-img 
        position relative
        img 
            width 100%
            display block
    &-content
        padding .2rem 
</style>



